___CSS_LOADER_EXPORT___.push([module.id, ".drag-container[data-v-4dd11bb2]{height:100px}label[data-v-4dd11bb2]{border:3px dashed #304ffe;border-radius:7px;color:#0026ca;height:8em;text-align:center}", "",{"version":3,"sources":["webpack://./resources/js/components/FileUpload.vue"],"names":[],"mappings":"AAkFA,iCACA,YACA,CACA,uBAGA,yBAAA,CAIA,iBAAA,CADA,aAAA,CAJA,UAAA,CAGA,iBAGA","sourcesContent":["<template>\r\n    <div id=\"file-drag-drop\">\r\n        <div class=\"drag-container\">\r\n            <input type=\"file\" name=\"file\" id=\"file\" @change=\"onChange\" ref=\"file\" hidden />\r\n\r\n            <label for=\"file\" class=\"bg-white w-100\" @dragover=\"dragover\" @dragleave=\"dragleave\" @drop=\"drop\">\r\n                <div class=\"d-flex justify-content-center align-items-center h-100\">\r\n                    <div class=\"p-3\" v-if=\"!file.name\">\r\n                        Drop file here\r\n                        or <u>click here</u> to upload file\r\n                    </div>\r\n                    <div class=\"media-body p-3\" v-else>\r\n                        <h5 class=\"mt-0 mb-1\">{{ filename }}</h5>\r\n                        <a class=\"btn btn-danger\" role=\"button\" v-on:click=\"remove\">Remove</a>\r\n                    </div>\r\n                </div>\r\n\r\n            </label>\r\n\r\n            <!-- <ul class=\"mt-4\" v-if=\"this.filelist.length\" v-cloak>\r\n                <li class=\"text-sm p-1\" v-for=\"file in filelist\" :key=\"file\">\r\n                    ${ file.name }<button class=\"ml-2\" type=\"button\" @click=\"remove(filelist.indexOf(file))\"\r\n                        title=\"Remove file\">remove</button>\r\n                </li>\r\n            </ul> -->\r\n        </div>\r\n    </div>\r\n</template>\r\n\r\n<script>\r\n    export default {\r\n        data() {\r\n            return {\r\n                file: {},\r\n                filename: '',\r\n                filelist: []\r\n            }\r\n        },\r\n        methods: {\r\n            onChange() {\r\n                this.file = this.$refs.file.files[0];\r\n                this.filename = this.file.name;\r\n            },\r\n            remove() {\r\n                this.filename = '';\r\n                this.file = {};\r\n            },\r\n            dragover(event) {\r\n                event.preventDefault();\r\n                if (!event.currentTarget.classList.contains('bg-info')) {\r\n                    event.currentTarget.classList.remove('bg-white');\r\n                    event.currentTarget.classList.add('bg-info');\r\n                }\r\n            },\r\n            dragleave(event) {\r\n                // Clean up\r\n                event.currentTarget.classList.add('bg-white');\r\n                event.currentTarget.classList.remove('bg-info');\r\n            },\r\n            drop(event) {\r\n                event.preventDefault();\r\n                this.$refs.file.files = event.dataTransfer.files;\r\n                this.onChange(); // Trigger the onChange event manually\r\n                // Clean up\r\n                this.dragleave(event);\r\n            }\r\n        }\r\n    }\r\n\r\n</script>\r\n\r\n<style scoped>\r\n    /* #wrapper {\r\n  margin:0 auto;\r\n}\r\n#fileDropBox {\r\n  line-height: 13em;\r\n  border: 3px dashed #1fab89;\r\n  text-align: center;\r\n  color: #1fab89;\r\n  border-radius: 7px;\r\n} */\r\n    .drag-container {\r\n        height: 100px;\r\n    }\r\n    label {\r\n        /* line-height: 8em; */\r\n        height: 8em;\r\n        border: 3px dashed #304ffe;\r\n        /* width: 500%; */\r\n        text-align: center;\r\n        color: #0026ca;\r\n        border-radius: 7px;\r\n    }\r\n\r\n    /* div.file-listing {\r\n        width: 400px;\r\n        margin: auto;\r\n        padding: 10px;\r\n        border-bottom: 1px solid #ddd;\r\n    }\r\n\r\n    div.file-listing img {\r\n        height: 100px;\r\n    }\r\n\r\n    div.remove-container {\r\n        text-align: center;\r\n    }\r\n\r\n    div.remove-container a {\r\n        color: red;\r\n        cursor: pointer;\r\n    }\r\n\r\n    a.submit-button {\r\n        display: block;\r\n        margin: auto;\r\n        text-align: center;\r\n        width: 200px;\r\n        padding: 10px;\r\n        text-transform: uppercase;\r\n        background-color: #CCC;\r\n        color: white;\r\n        font-weight: bold;\r\n        margin-top: 20px;\r\n    }\r\n\r\n    progress {\r\n        width: 400px;\r\n        margin: auto;\r\n        display: block;\r\n        margin-top: 20px;\r\n        margin-bottom: 20px;\r\n    } */\r\n\r\n</style>\r\n"],"sourceRoot":""}]);
